import React from 'react';
import classNames from 'classnames';

export interface SvgProps extends React.HTMLAttributes<HTMLOrSVGElement> {
  readonly width?: number;
  readonly height?: number;
}

export const SvgIcon: React.FC<SvgProps> = ({
  className,
  children,
  width = 24,
  height = 24,
  ...props
}) => (
  <svg
    className={classNames(
      'fill-current w-1em h-1em inline-block text-2xl transition-all flex-shrink-0 select-none',
      className
    )}
    focusable="false"
    viewBox={`0 0 ${width} ${height}`}
    aria-hidden="true"
    {...props}
  >
    {children}
  </svg>
);
